import React, { useEffect, useState, useRef, Suspense } from "react";
import { createPortal } from "react-dom";
import ReactDOM from "react-dom";
import Draggable from "react-draggable";
import { Canvas } from "@react-three/fiber";
import { OrbitControls, useGLTF } from "@react-three/drei";
import styles from "./ThreeDModelWindow.module.css";
import { extend, ReactThreeFiber } from "@react-three/fiber";
import * as THREE from "three";
import { useLoader, useFrame } from "@react-three/fiber";
import { GLTFLoader } from "three/examples/jsm/loaders/GLTFLoader.js";
import { WindowName } from "types/types";
import { Rnd } from "react-rnd";


const API_URL = process.env.REACT_APP_BACKEND_URL || "http://127.0.0.1:5000"; // ✅ Ensure correct backend URL

interface ThreeDModelWindowProps {
  profileId: number;
  zIndex: number;
  onMouseDown: () => void;
  onClose: () => void;
  bringWindowToFront: (windowName: WindowName) => void;
  windowZIndexes: { [key: string]: number };
  zIndexCounter: number;
  showThreeDModelWindow: boolean; // ✅ Accept show state as a prop
}

interface SatelliteModelProps {
  modelPath: string;
  scale?: number;
  position?: [number, number, number];
  onColorExtracted?: (color: THREE.Color) => void;
}

const SatelliteModel: React.FC<SatelliteModelProps> = ({ modelPath, scale = 40, position = [0, 0, 0], onColorExtracted }) => {
  if (!modelPath) {
    console.warn("⚠️ No model path provided, showing fallback.");
    return <p className="error-message">⚠️ No model path provided</p>;
  }

  console.log(`🔍 Attempting to load GLTF model from: ${modelPath}`);

  const ref = useRef<THREE.Group | null>(null);
  const gltf = useLoader(GLTFLoader, modelPath);
  const [hovered, setHovered] = useState(false);

  useEffect(() => {
    if (gltf.scene && onColorExtracted) {
      const firstMesh = gltf.scene.children.find(
        (obj) => obj instanceof THREE.Mesh
      ) as THREE.Mesh | undefined;
      if (firstMesh && firstMesh.material instanceof THREE.MeshStandardMaterial) {
        const color = new THREE.Color(firstMesh.material.color.getHex());
        onColorExtracted(color);
      }
    }
  }, [gltf, onColorExtracted]);

  // Rotate model slightly over time
  useFrame(() => {
    if (ref.current) {
      (ref.current as THREE.Group).rotation.y += 0.003;
    }
  });
  
  return (
    <primitive
      ref={ref}
      object={gltf.scene}
      position={position}
      scale={hovered ? scale * 1.2 : scale}
      onPointerOver={() => setHovered(true)}
      onPointerOut={() => setHovered(false)}
    />
  );
};

export { SatelliteModel };

const ThreeDModelWindow: React.FC<ThreeDModelWindowProps> = ({
  profileId,
  zIndex,
  onMouseDown,
  onClose,
  bringWindowToFront,
  windowZIndexes,
  zIndexCounter,
  showThreeDModelWindow,
}) => {
  const [isDarkMode, setIsDarkMode] = useState(false);
  const [modelPath, setModelPath] = useState<string | null>(null);
  const [errorMessage, setErrorMessage] = useState<string | null>(null);
  const fileInputRef = useRef<HTMLInputElement>(null);
  const nodeRef = useRef<HTMLDivElement>(null!);
  const [selectedFile, setSelectedFile] = useState<File | null>(null);
  const [canvasBgColor, setCanvasBgColor] = useState("#ffffff"); // For Canvas background
  const [modelLoading, setModelLoading] = useState(true);
  const [modelError, setModelError] = useState<string | null>(null);
  const [position, setPosition] = useState({
    x: (window.innerWidth - 800) / 2,
    y: (window.innerHeight - 600) / 2
  });
  const [uploadStatus, setUploadStatus] = useState<string | null>(null);
  const windowName = "ThreeDModelWindow";

  // Create portal element
  const [portalElement] = useState(() => {
    const element = document.createElement("div");
    element.id = "threeDModelWindow-root";
    document.body.appendChild(element);
    return element;
  });

  // Clean up portal on unmount
  useEffect(() => {
    return () => {
      if (portalElement && portalElement.parentNode) {
        portalElement.parentNode.removeChild(portalElement);
      }
    };
  }, [portalElement]);

  // Fetch model path
  useEffect(() => {
    if (!profileId) return;
    
    const fetchModelData = async () => {
      setModelLoading(true);
      setModelError(null);
    
      try {
        console.log(`🌐 Fetching model for profile ID: ${profileId}`);
        const backendUrl = process.env.REACT_APP_BACKEND_URL || "http://127.0.0.1:5000";
        const response = await fetch(`${backendUrl}/api/profile/${profileId}`);
        
        if (!response.ok) {
          throw new Error(`Failed to fetch model data: ${response.status}`);
        }
        
        const data = await response.json();
        console.log(`📥 Received model data:`, data);
        
        if (data.model_path) {
          setModelPath(data.model_path);
        } else {
          setModelError("No 3D model available for this profile");
        }
      } catch (error) {
        console.error("Error fetching model:", error);
        setModelError(error instanceof Error ? error.message : "Failed to load model");
      } finally {
        setModelLoading(false);
      }
    };
    
    fetchModelData();
  }, [profileId]);

  // Check dark mode
  useEffect(() => {
    const checkDarkMode = () => {
      setIsDarkMode(document.documentElement.classList.contains("dark"));
    };
    checkDarkMode();
    
    // Set up observer for theme changes
    const observer = new MutationObserver((mutations) => {
      mutations.forEach((mutation) => {
        if (mutation.attributeName === "class") {
          checkDarkMode();
        }
      });
    });

    observer.observe(document.documentElement, {
      attributes: true,
      attributeFilter: ["class"]
    });

    return () => observer.disconnect();
  }, []);
    
  // Get the effective z-index value
  const effectiveZIndex = windowZIndexes["ThreeDModelWindow"] || zIndex;
  
  // When the window is clicked, bring it to front
  const handleWindowClick = () => {
    console.log("🖱️ Clicked ThreeDModelWindow, bringing to front");
    onMouseDown();
  };
  
  // Handle file upload
  const handleFileUpload = async () => {
    if (!selectedFile) return;

    // Check if file is a .glb model
    if (!selectedFile.name.endsWith('.glb')) {
      setUploadStatus('Error: Only .glb files are supported.');
      return;
    }

    setModelLoading(true);
    setUploadStatus('Uploading model...');

    // Create FormData for the upload
    const formData = new FormData();
    formData.append('file', selectedFile);
    formData.append('profile_id', profileId.toString());

    try {
      const backendUrl = process.env.REACT_APP_BACKEND_URL || "http://127.0.0.1:5000";
      const response = await fetch(`${backendUrl}/api/upload-glb`, {
        method: 'POST',
        body: formData,
      });

      if (!response.ok) {
        const errorData = await response.json();
        throw new Error(errorData.error || 'Failed to upload model');
      }

      const result = await response.json();
      setModelPath(result.model_path);
      setUploadStatus('Model uploaded successfully!');
      setModelError(null);
      
      // Add a small delay before hiding the success message
      setTimeout(() => {
        setUploadStatus(null);
      }, 3000);
    } catch (error) {
      console.error('Error uploading model:', error);
      setUploadStatus(`Error: ${error instanceof Error ? error.message : 'Failed to upload model'}`);
      setModelError(error instanceof Error ? error.message : 'Failed to upload model');
    } finally {
      setModelLoading(false);
    }
  };

  // Debug mount/unmount
  useEffect(() => {
    console.log(`ThreeDModelWindow mounted, showThreeDModelWindow: ${showThreeDModelWindow}, profileId: ${profileId}`);
    return () => {
      console.log("ThreeDModelWindow unmounted");
    };
  }, [showThreeDModelWindow, profileId]);

  // Debug z-index
  useEffect(() => {
    console.log(`🎯 ThreeDModelWindow using z-index:`, effectiveZIndex);
  }, [effectiveZIndex]);

  if (!showThreeDModelWindow) {
    console.log("ThreeDModelWindow should not be visible, returning null");
    return null;
  }

  return createPortal(
    <Draggable
      nodeRef={nodeRef}
      handle=".drag-handle"
      position={position}
      onStop={(e, data) => {
        console.log(`📌 ThreeDModelWindow moved to: x=${data.x}, y=${data.y}`);
        setPosition({ x: data.x, y: data.y });
      }}
    >
      <div 
        ref={nodeRef} 
        className={styles.popup}
        style={{ 
          position: "fixed", 
          zIndex: effectiveZIndex
        }}
        onClick={handleWindowClick}
      >
        <div className={`${styles.header} drag-handle`}>
          <h2>Satellite Model Viewer</h2>
          <button
            className={styles.closeButton}
            style={{ color: isDarkMode ? "white" : "black" }}
            onClick={(e) => {
              e.stopPropagation();
              onClose();
            }}
          >
            ✖
          </button>
        </div>
        <div className={styles.content}>
          {/* Show upload status if any */}
          {uploadStatus && (
            <div style={{ 
              marginTop: "10px", 
              marginBottom: "10px",
              padding: "8px", 
              borderRadius: "4px",
              backgroundColor: uploadStatus.includes('Error') 
                ? (isDarkMode ? "#3d0000" : "#ffdddd") 
                : (isDarkMode ? "#004d00" : "#ddffdd"),
              color: uploadStatus.includes('Error')
                ? (isDarkMode ? "#ff8080" : "#d00000")
                : (isDarkMode ? "#80ff80" : "#00b000")
            }}>
              {uploadStatus}
            </div>
          )}

          {/* Show Error Message or 3D Model */}
          {modelLoading ? (
            <div style={{ padding: "20px" }}>Loading 3D model...</div>
          ) : modelError || errorMessage ? (
            <p className={styles.errorMessage}>{modelError || errorMessage}</p>
          ) : modelPath ? (
            <Canvas
              camera={{ position: [5, 5, 10], fov: 50 }}
              style={{ width: "100%", height: "400px", backgroundColor: canvasBgColor }}
            >
              <ambientLight intensity={0.8} />
              <directionalLight position={[10, 10, 10]} />
              <SatelliteModel modelPath={modelPath} onColorExtracted={(color) => setCanvasBgColor(color.getStyle())} />
              <OrbitControls />
            </Canvas>
          ) : (
            <p className={styles.errorMessage}>⚠️ No file uploaded</p>
          )}
  
          {/* Upload File Section */}
          <div className={styles.uploadSection}>
            <p>Accepted formats: <b>.glb</b></p>
            <input 
              type="file" 
              accept=".glb" 
              ref={fileInputRef} 
              onChange={(event) => event.target.files && setSelectedFile(event.target.files[0])}
              className={styles.uploadInput} 
              onClick={(e) => e.stopPropagation()}
            />
            
            {selectedFile && (
              <button className={styles.uploadButton} onClick={handleFileUpload}>
                Upload 3D Model file
              </button>
            )}
          </div>
        </div>
      </div>
    </Draggable>,
    portalElement
  );
};

export default ThreeDModelWindow;
